<template>
  <div v-if="shippingTrackingForm == 'canAdd'" class="lg:opacity-0 group-hover:opacity-100 transition-opacity" @click.stop>
    <Button color="teal" @click="add()" dense><span class="material-icons text-xl">add</span> Add</Button>
  </div>
  <div v-else-if="shippingTrackingForm == 'canUpdate'">
    <div class="grid grid-cols-2 gap-4">
      <div>
        <Input class="w-full" v-model="newData.shipping_tracking" placeholder="input" />
        <span v-for="error of validation.shipping_tracking.$errors" class="text-red-base mt-2 block" :key="error">{{ error.$message }}. </span>
      </div>
      <div>
        <Button color="teal" class="mt-2 mr-2" :loading="loading.update_shipping_tracking" @click="save(data)" dense><i class="material-icons text-lg">check</i></Button>
        <Button color="red" class="mt-2" :loading="loading.update_shipping_tracking" @click="cancel()" dense><i class="material-icons text-lg">close</i></Button>
      </div>
    </div>
  </div>
  <div v-else>
    <div class="grid grid-cols-2 gap-4">
      <div>
        <a
          v-if="data.shipping_tracking_link"
          :href="
            data.shipping_tracking_link ? data.shipping_tracking_link : '#'
          "
          class="float-left mt-2"
          color="teal"
          >{{ data.shipping_tracking }}</a
        >
        <span v-else>{{ data.shipping_tracking }}</span>
      </div>
      <div>
        <Button color="teal" class="mr-2" :loading="loading.delete_shipping_tracking" @click="edit(data.shipping_tracking)" dense><i class="material-icons text-lg">edit</i></Button>
        <Popover width="400" trigger="hover">
          <div class="w-full h-full flex justify-center items-center">
            <template v-if="true">
              <span class="whitespace-pre-line">Delete Tracking Number</span>
            </template>
          </div>
          <template #reference>
            <Button  color="red" :loading="loading.delete_shipping_tracking" @click="remove(data)" dense><i class="material-icons text-lg">delete</i></Button>
          </template>
        </Popover>

      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, reactive, watch } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import { useAll } from 'composable/invoice'
import { useVuelidate } from '@vuelidate/core'
import { required } from "@vuelidate/validators"
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Input,
    Button
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        box_id: null,
        shipping_tracking: null,
      })
    },
  },
  setup(props, { emit }) {
    const { loading, actionShippingTrackingUpdate, actionShippingTrackingDelete } = useAll('invoice')
    const data = ref(props.data)
    const shippingTrackingForm = ref(data.value.shipping_tracking == null ? 'canAdd' : 'canEdit')
    const newData = ref({
      shipping_tracking: null
    })

    const add = () => {
      shippingTrackingForm.value = 'canUpdate'
    }

    const rules = {
      shipping_tracking: { required }
    }
    
    const validation = useVuelidate(rules, newData, {
      $lazy: true
    })

    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    const save = async (data) => {
      if (!validate()) {
        return;
      }

      const response = await actionShippingTrackingUpdate({
        box_id: data.box_id,
        data: {
          shipping_tracking: newData.value.shipping_tracking
        }
      })

      if (response) {
        ElMessage({
          type: 'success',
          message: 'Tracking number saved.',
        })
        shippingTrackingForm.value = 'canEdit'
        data.shipping_tracking = newData.value.shipping_tracking
      }
    }

    const cancel = () => {
      if (newData.value.shipping_tracking) {
        shippingTrackingForm.value = 'canEdit'
      } else {
        shippingTrackingForm.value = 'canAdd'
      }
    }

    const edit = (shipping_tracking) => {
      shippingTrackingForm.value = 'canUpdate'
      newData.value.shipping_tracking = shipping_tracking
    }

    const remove = async (data) => {
      const response = await actionShippingTrackingDelete({
        box_id: data.box_id
      })

      if (response) {
        ElMessage({
          type: 'success',
          message: 'Tracking number removed.',
        })
        newData.value.shipping_tracking = null
        shippingTrackingForm.value = 'canAdd'
      }
    }

    watch(() => props.data, () => {
      if (props.data) data.value = props.data
        else data.value = { box_id: null, shipping_tracking: null }
    }, { immediate: true })

    return {
      loading,
      data,
      add,
      save,
      cancel,
      edit,
      remove,
      newData,
      validation,
      validate,
      shippingTrackingForm
    }
  }
})
</script>