<template>
  <div>
    <div class="flex xl:flex-row flex-col justify-between mb-8 items-center">
      <div class="lg:flex lg:divide-x lg:divide-grey-01 xl:mb-0 mb-4">
        <div class="flex lg:pr-4 lg:mb-0 mb-4">
          <h5>Invoices</h5>
        </div>
      </div>
      <div class="lg:space-x-4 lg:space-y-0 space-y-4">
        <Button color="teal" class="lg:inline-block block" :to="{ name: 'invoiceCreate' }" link><span class="material-icons">add</span> Add New Invoice</Button>
      </div>
    </div>
    <Card>
      <Tab view="smooth" :items="tab" />
      <div v-loading="loading.all" class="grid grid-cols-2 gap-4" v-if="route.params.statusId === '1'">
        <Alert color="teal" view="smooth" dense class="mb-5" v-if="route.params.statusId === '1'">
          <p class="text-lg" style="color: #424242;">
            Total Outstanding Balance Due 
          </p>
          <div class="flex items-center">
            <div class="text-4xl" style="color: #424242">
              <span class="font-semibold">
                <Currency :value="totalBalanceDue" />
              </span>
            </div>
          </div>
          <template #left>
            <div class="flex items-center h-full">
              <img src="@/assets/balance-due.svg" alt="Icon - balance - due" class="h-20 mr-5">
            </div>
          </template>
        </Alert>
        <Alert color="green" view="smooth" dense class="mb-5" v-if="route.params.statusId === '1'">
          <p class="text-lg" style="color: #48bb78">
            Total Outstanding Profit
          </p>
          <div class="flex items-center">
            <div class="text-4xl" style="color: #48bb78">
              <span class="font-semibold">
                <Currency :value="totalProfit" />
              </span>
            </div>
            <div>
              <span class="ml-2 p-2 rounded-lg text-base" color="white" style="color: white; background-color: #48bb78">{{table.avg_profit_percentage}} %</span>
            </div>
          </div>
          <template #left>
            <div class="flex items-center h-full">
              <img src="@/assets/profit.svg" alt="Icon - profit" class="h-20 mr-5">
            </div>
          </template>
        </Alert>
      </div>
      <div class="flex flex-col md:flex-row mb-6">
        <div class="flex-grow md:mr-3 md:mb-0 mb-3">
          <Input v-model="data.search" placeholder="Search Everything" class="w-full" icon>
            <template #icon>
              <span class="material-icons">search</span>
            </template>
          </Input>
        </div>
        <div class="flew-grow-0">
          <GroupContainer>
            <Button color="white" view="outlined" class="flex-auto" @click="filterDateFocus" @blur="filterDateFocus">
              <DatePicker v-model="data.date" v-bind="filterDateOptions" type="daterange" ref="filterDateRef" class="hide-picker" /> Select Date
            </Button>
            <Dropdown trigger="click" size="medium" :fallbackPlacements="['bottom']">
              <Button color="white" view="outlined" class="flex-auto dropdown dropdown-arrow" :loading="loadingSalesPerson">Sales Person</Button>
              <template #dropdown>
                <DropdownMenu>
                  <DropdownItem v-for="(sales, salesPersonIndex) in salesPerson" :key="salesPersonIndex">
                    <Checkbox color="teal" v-model="data.sales_person" :value="sales">
                      {{ sales.sales_person }}
                    </Checkbox>
                  </DropdownItem>
                </DropdownMenu>
              </template>
            </Dropdown>
          </GroupContainer>
        </div>
      </div>
      <div class="flex items-center flex-row mb-6">
        <div class="inline-block" v-for="(filter, filterKey) of filterList" :key="filterKey">
          <Button v-if="filter.value" color="grey" view="smooth" dense class="mx-1">
            <b>{{filter.name}}:</b> {{ filter.value }}<i class="material-icons text-sm" @click="clearFilter(filter)">close</i>
          </Button>
        </div>
        <div class="inline-block ml-3">
          <Button v-if="isFiltered" color="teal" view="smooth" @click="clearAllFilter" dense>
            <i class="material-icons text-sm mr-1">close</i>Clear all
          </Button>
        </div>
      </div>
      <Table class="mb-6" :data="table.data" v-loading="loading.all" highlight-current-row  :show-summary="!checkRouterIsALL && !loading.all" :summary-method="tableSummary">
        <TableColumn prop="box_id" label="Invoice#">
          <template #default="scope">
            <router-link tag="a" class="font-bold" :to="{ name: 'invoiceDetail', params: { buybackId: scope.row.box_id } }">{{ scope.row.box_id }}</router-link>
          </template>
        </TableColumn>
        <TableColumn v-if="route.params.statusId === 'all'" prop="status_invoice" label="Status" width="150">
          <template #default="scope">
            <!-- <Badge :color="color(scope.row.order_status.short_name)">{{ scope.row.order_status.short_name }}</Badge> -->
            <Badge class="whitespace-nowrap" :color="scope.row.status_invoice === 'Paid' ? 'teal' : 'red'">{{ scope.row.status_invoice }}</Badge>
            <!-- <Badge v-if="scope.row.due_date == ''" class="whitespace-nowrap" color="green">Draft</Badge> -->
            <!-- <Badge class="whitespace-nowrap" v-else color="red">Outstanding</Badge> -->
          </template>
        </TableColumn>
        <TableColumn label="Bill To" width="150">
          <template #default="scope">
            <TextLimit :text="scope.row.customer_fullname|| ''">
              <template #default="{ text }">
                <div v-if="nameCapitalize(text).length" class="mb-2">
                  <span v-for="(name, n) in nameCapitalize(text)" :key="n" class="text-teal-04 capitalize">
                    {{ `${name} ` }}
                  </span>
                </div>
                <span v-else class="block text-teal-04 mb-2">{{ text }}</span>
              </template>
            </TextLimit>
          </template>
        </TableColumn>
        <TableColumn prop="sales_person" label="Sales Person" width="150">
          <template #default="scope">
            <TextLimit :text="scope.row.sales_person || ''" />
          </template>
        </TableColumn>
        <TableColumn prop="invoice_date" label="Invoice Date" width="100" />
        <TableColumn width="100" :label="route.params.statusId === '3' && 'Date Paid' || 'Due Date'">
          <template #default="scope">
            {{ route.params.statusId == 3 ? scope.row.closed_date : scope.row.due_date }}
          </template>
        </TableColumn>
        <!-- <TableColumn v-if="route.params.statusId !== '3'" prop="due_paid" label="Due Paid" >
          <template #default="scope">
            <Currency :value="scope.row.due_paid" />
          </template>
        </TableColumn> -->
        <!--    <TableColumn prop="qty" label="Item Quantity" />-->
        <TableColumn prop="sub_total" label="Total" width="200" align="right">
          <template #default="scope">
            <Currency :value="scope.row.sub_total" />
          </template>
        </TableColumn>
        <TableColumn v-if="route.params.statusId === '1'" prop="balance_due" label="Balance Due" width="200" align="right">
          <template #default="scope">
            <Currency :value="scope.row.balance_due" />
          </template>
        </TableColumn>
        <TableColumn prop="profit" v-if="route.params.statusId === '3' || route.params.statusId === '1'" label="Profit" align="right">
          <template #default="scope">
            <Currency :value="scope.row.profit" />
          </template>
        </TableColumn>
        <TableColumn prop="profit_percentage" v-if="route.params.statusId === '3'" label="Profit Margin" width="100" align="right">
          <template #default="scope">
            {{scope.row.profit_percentage}}%
          </template>
        </TableColumn>
        <TableColumn v-if="route.params.statusId !== '3'" prop="shipping_tracking" label="Tracking Number" width="300">
          <template #default="scope">
            <ShippingTracking
              :data="scope.row"
            />
          </template>
        </TableColumn>
        <TableColumn v-if="route.params.statusId !== '3'" prop="action" label="">
          <template #default="scope">
            <div v-if="scope.row.status_invoice !== 'Paid'" class="lg:opacity-0 group-hover:opacity-100 transition-opacity" @click.stop>
              <Popover v-if="scope.row.is_can_deleted !== 1" placement="top-end" trigger="hover" :width="200">
                <div class="text-sm text-black whitespace-nowrap">
                  Delete all products off invoice first
                </div>
                <template #reference>
                  <div class="inline-block">
                    <Button disabled  color="red" dense><span class="material-icons text-xl">delete</span></Button>
                  </div>
                </template>
              </Popover>
              <Button v-else color="red" @click="openModalSelected('delete', scope.row)" dense><span class="material-icons text-xl">delete</span></Button>
            </div>
          </template>
        </TableColumn>
      </Table>
      <Pagination v-model:page="data.page" v-model:limit="data.limit" :data="table" />
    </Card>
    <Modal size="xs" v-model="modal.delete" title="Delete Invoice" @close="unsetData" hide-header hide-footer>
      <template #default>
        <div class="text-center py-10">
          <img class="mb-10 mx-auto" src="@/assets/icon/attention.svg" alt="Icon">
          <div class="mt-8 mb-10">
            <h6 class="mb-2">Attention!</h6>
            <p>Do you want to delete this invoice data?</p>
          </div>
          <Button color="red" class="mr-5" :loading="loading.delete" @click="deleteInvoice">Delete Invoice</Button>
          <Button view="outlined" color="teal" :disabled="loading.delete" @click="modal.delete = false">Cancel</Button>
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { defineComponent, watch, computed, ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStatuses, useAll, useSalesPerson } from 'composable/invoice'
import { useUser, useLogin } from 'composable/user'
import { currency } from 'utils/number'
import { color } from 'utils/status'
import debounce from 'utils/debounce'
import { nameCapitalize } from 'utils/element'
import Tab from 'bbUi/components/tab/tab.vue'
import Badge from 'bbUi/components/badge/badge.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Pagination from 'components/Pagination.vue'
import Currency from 'components/Currency.vue'
import TextLimit from 'components/TextLimit.vue'
import ShippingTracking from 'components/Invoice/ShippingTracking.vue'
import Alert from 'bbUi/components/alert/alert.vue'

export default defineComponent({
  components: {
    Tab,
    Badge,
    GroupContainer,
    Button,
    Input,
    Checkbox,
    Select,
    Modal,
    Card,
    Pagination,
    Currency,
    TextLimit,
    ShippingTracking,
    Alert
  },
  async setup() {
    const { tab } = useStatuses('invoice')
    const { me } = useUser()
    const { datamatchStatus, statusLogin } = useLogin()
    const { data, selected, state: table, actionAll, actionDelete, filterDateRef, filterDateOptions, filterDateFocus, filterList, clearFilter, clearAllFilter, isFiltered, unsetData, openModal, loading, modal } = useAll('invoice')
    const { state: salesPerson, loading: loadingSalesPerson, action: actionSalesPerson } = useSalesPerson('invoice')
    const route = useRoute()
    const router = useRouter()
    const totalBalanceDue = ref(0);
    const totalProfit = ref(0);

    // if (datamatchStatus.value == 1) {
    //   router.push({ name: 'invoiceDatamatch' })
    // }
    data.search = window.localStorage.getItem('invoice-tab-search') ? window.localStorage.getItem('invoice-tab-search') : ''
    data.date = window.localStorage.getItem('invoice-tab-date') == 'null' ? '' : window.localStorage.getItem('invoice-tab-date')
    data.sales_person = JSON.parse(window.localStorage.getItem('invoice-tab-sales-person') ? window.localStorage.getItem('invoice-tab-sales-person') : '[]')

    actionSalesPerson()

    watch(() => ({
      route: route.params.statusId,
      limit: data.limit,
      search: data.search,
      date: data.date
    }), () => {
      data.page = 1
    }, {
      deep: true
    })
    watch(() => ({
      route: route.params.statusId,
      ...data
    }), debounce(async () => {
      if(route.name === 'invoiceTab') {
        window.localStorage.setItem('invoice-tab-search', data.search)
        window.localStorage.setItem('invoice-tab-date', data.date)
        window.localStorage.setItem('invoice-tab-sales-person', JSON.stringify(data.sales_person))

        await actionAll(route.params.statusId)

        totalBalanceDue.value = table.value.total_balance_due ? table.value.total_balance_due : 0;
        totalProfit.value = table.value.total_profit ? table.value.total_profit : 0;
      }
    }, 500), {
      immediate: true,
      deep: true
    })
    const checkRouterIsALL = computed(() => {
      if (route.params.statusId === 'all') {
        return true
      }
      return false
    })
    const deleteInvoice = async () => {
      await actionDelete(route.params.statusId)
    }
    const openModalSelected = (target, item) => openModal(target, () => {
      selected.value = item.box_id
    })
    const tableSummary = ({columns}) => {
      if (!checkRouterIsALL) return ''

      return columns.map(column => column.property === 'due_date' ? `Total` : '' || column.property === 'sub_total' ? `${currency(me.value, table.value.grand_total)}` : '' || column.property === 'balance_due' ? `${currency(me.value, table.value.total_balance_due)}` : '' || column.property === 'profit' ? `${currency(me.value, table.value.total_profit)}` : '' || column.property === 'profit_percentage' ? `${table.value.avg_profit_percentage}%` : '')
    }
    const redirectToOrder = (data) => {
      router.push({
        name: 'invoiceDetail',
        params: {
          buybackId: data.box_id
        }
      })
    }

    return {
      me,
      datamatchStatus,
      route,
      data,
      salesPerson,
      loading,
      loadingSalesPerson,
      modal,
      table,
      tab,
      filterList,
      isFiltered,
      unsetData,
      filterDateRef,
      filterDateOptions,
      filterDateFocus,
      clearFilter,
      clearAllFilter,
      deleteInvoice,
      openModalSelected,
      color,
      tableSummary,
      redirectToOrder,
      checkRouterIsALL,
      totalBalanceDue,
      nameCapitalize,
      totalProfit
    }
  }
})
</script>